export default "<template>\r\n  <d-row gap>\r\n    <d-card v-for=\"size in sizes\" :key=\"size\" elevation-dark=\"4\" elevation-light=\"n4\" outlined depressed width=\"100px\"\r\n            :height=\"size === 'pill'?'50px':'100px'\"\r\n            :rounded=\"size\">\r\n      <d-card-subtitle class=\"pa-3\">{{ size }}</d-card-subtitle>\r\n    </d-card>\r\n  </d-row>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"Variants\",\r\n\r\n  data: () => ({\r\n    sizes: [\r\n      'none',\r\n      'md',\r\n      'lg',\r\n      'xl',\r\n      'pill',\r\n      'circle'\r\n    ]\r\n  })\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n\r\n</style>\r\n";